.education-section {
    background-color: #f4f4f4;
    padding: 60px 20px;
    text-align: left;
  }
  
  .education-content {
    max-width: 900px;
    margin: 0 auto;
  }
  
  .education-item {
    margin-bottom: 40px;
  }
  
  h1 {
    font-size: 36px;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #444;
  }
  
  p {
    font-size: 18px;
    line-height: 1.6;
    color: #666;
  }
  
  ul {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  li {
    font-size: 18px;
    color: #555;
  }
  