.about-section {
    background-color: #f9f9f9;
    padding: 60px 20px;
    text-align: center;
    padding: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-content {
    max-width: 900px;
    margin: 0 auto;
    display: flex;
    max-width: 1200px;
    width: 100%;
  }
  
  
  h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
  }
  
  p{
    font-size: 18px;
    line-height: 1.6;
    color: #555;
    margin-bottom: 20px;
    text-align: center;
  }
  .seeni-image{
   border-radius: 2px;   
    margin: 20 px 0;
    margin-left:50px;
    margin-right: 250px;
    width: 300px;
    height: 300px;
    border-radius: 100%;
    object-fit: cover; 
  }
  
    
  
  .about-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  .about-text {
    flex: 2;
    padding-left: 40px;
  }
  
  .p-section {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 20px;
    text-align: center;
  }
  